/* Base for label styling */
[type="checkbox"]:not(:checked), [type="checkbox"]:checked { position: absolute; left: -9999px; }

[type="checkbox"]:not(:checked) + label, [type="checkbox"]:checked + label { position: relative; padding-left: 32px; font-size: 18px; line-height: 24px; vertical-align: middle; cursor: pointer; }

/* checkbox aspect */
[type="checkbox"]:not(:checked) + label:before, [type="checkbox"]:checked + label:before { content: ""; position: absolute; left: 0; top: 0; width: 20px; height: 20px; border: 2px solid #fec200; border-radius: 100%; transition: all 0.2s; }

/* checked mark aspect */
[type="checkbox"]:not(:checked) + label:after, [type="checkbox"]:checked + label:after { content: ""; position: absolute; top: 4px; left: 4px; width: 16px; height: 16px; background-color: #fec200; border-radius: 100%; transition: all 0.5s; }

/* checked mark aspect changes */
[type="checkbox"]:not(:checked) + label:after { opacity: 0; transform: scale(0); }

[type="checkbox"]:checked + label:after { opacity: 1; transform: scale(1); }

/* disabled checkbox */
[type="checkbox"]:disabled:not(:checked) + label:before, [type="checkbox"]:disabled:checked + label:before { box-shadow: none; border-color: #bbb; background-color: #ddd; }

[type="checkbox"]:disabled:checked + label:after { color: #999; }

[type="checkbox"]:disabled + label { color: #aaa; }

